.logo {
    height: 32px;
    width: 132px;
    background: rgba(255,255,255,.7);
    margin: 16px;
}

.flex-col {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: stretch;
            align-items: stretch;
    padding-bottom: 10px;
}

.flex-row {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

